import React from 'react';
import {Link} from 'react-router-dom';
import NavItem from './NavItem.jsx';
import HamburgerMenu from './HamburgerMenu.jsx';

export default class MainNav extends React.Component {
  constructor (props) {
    super(props);
    this.state = {}
  }

  render () {
    var navClass = '';
    var iconClass = '';
    
    if(this.props.currentSection !== '')
      navClass = 'content-nav';

    if(this.props.currentSection === 'about')
      iconClass = 'to-about';

    if(this.props.lastSection !== this.props.currentSection && this.props.lastSection === 'about')
      iconClass = 'from-about';

    return (
      <nav className={"navbar_ " + navClass}>
        <div className="navmenu_">
          <ul className="navmenu">
            <NavItem title="About" setCurrentSection={this.props.setCurrentSection} />
            <NavItem title="Resume" setCurrentSection={this.props.setCurrentSection} />
            <li className="chibi-jdiu_">
              <Link to={'/'} onClick={() => {this.props.setCurrentSection('');}} className={"chibi-jdiu " + iconClass}>
                <div className="text-logo">jdiu</div>
                <div className="chibi-img"><img src="imgs/jason.png" alt="Jason Diu cartoon" /></div>
              </Link>
            </li>
            <NavItem title="Projects" setCurrentSection={this.props.setCurrentSection} />
            <NavItem title="Contact" setCurrentSection={this.props.setCurrentSection} />
            <HamburgerMenu toggleMobileNav={this.props.toggleMobileNav} 
                          mobileMenuActive={this.props.mobileMenuActive} />
          </ul>
        </div>
      </nav>
    );
  }
}