import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';
import MainNav from './components/MainNav.jsx';
import MobileNav from './components/MobileNav.jsx';
import HomeBackground from './components/HomeBackground.jsx';
import AboutPage from './components/AboutPage.jsx';
import ResumePage from './components/ResumePage.jsx';
import ProjectsPage from './components/ProjectsPage.jsx';
import ContactPage from './components/ContactPage.jsx';
import './style.css';

class Site extends React.Component {
  constructor () {
    super();
    this.state = {
      lastSection: window.location.pathname.replace(/^\/|\/$/g, ''),
      currentSection: window.location.pathname.replace(/^\/|\/$/g, ''),
      mobileMenuActive: false
    }
  }

  render () {
    var activeClass = this.state.mobileMenuActive ? 'main_-mobile' : '';

    return (
      <main className={"main_ " + activeClass}>
        <div onClick={this.closeMobileNav} className="wrappp">
          <MainNav lastSection={this.state.lastSection} 
                  currentSection={this.state.currentSection}
                  mobileMenuActive={this.state.mobileMenuActive}
                  setCurrentSection={this.setCurrentSection}
                  toggleMobileNav={this.toggleMobileNav} />
          <Route exact path='/' component={HomeBackground} />
          <Route render={({ location }) => (
            <div className="main-content">
              <CSSTransition
                classNames="fade"
                timeout={{ exit: 300, enter: 0.001}}
              >
                <Switch key={location.key} location={location}>
                  <Route path='/about' component={AboutPage} />
                  <Route path='/resume' component={ResumePage} />
                  <Route path='/projects' component={ProjectsPage} />
                  <Route path='/contact' component={ContactPage} />
                </Switch>
              </CSSTransition>
            </div>
          )}/>
        </div>
        <MobileNav setCurrentSection={this.setCurrentSection} 
                  toggleMobileNav={this.toggleMobileNav} />
      </main>
    );
  }

  setCurrentSection = (sectionName) => {
    this.setState({
      lastSection: this.state.currentSection,
      currentSection: sectionName
    });
  }

  toggleMobileNav = () => {
    this.setState({
      mobileMenuActive: !this.state.mobileMenuActive
    });
  }

  onHistoryMove = () => {
    this.setState({
      lastSection: this.state.currentSection,
      currentSection: window.location.pathname.replace(/^\/|\/$/g, '')
    });
  }

  componentDidMount = () => {
    // listen for when the back/forward button is pressed and call onHistoryMove
    window.onpopstate = () => {
      this.onHistoryMove();
    }
  }

  closeMobileNav = () => {
    if(this.state.mobileMenuActive){
      this.toggleMobileNav();
    }
  }
}

ReactDOM.render(
  <BrowserRouter basename={process.env.PUBLIC_URL}>
    <Site />
  </BrowserRouter>,
  document.getElementById('root')
);
